/**
 * A wrapper around `performance.now()` so that we can run unit tests
 * in Node.
 */

import GLOBAL from './global';

let now;
if ('performance' in GLOBAL === false) {
  let startTime = Date.now();
  now = () => Date.now() - startTime;
} else {
  now = () => performance.now();
}

export default now;
